body {
  height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.pagination-container {
  text-align: center;
  margin-bottom: 20px;
}

.pagination {
  padding: 16px;
  display: inline-block;
}

.page-button {
  color: #000;
  background-color: #f7f7f7;
  border: 1px solid #b92f2c;
  border-radius: 8px;
  margin: 5px;
  padding: 10px 20px;
  font-size: 16px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
}

.page-button:hover, .page-button.active {
  color: #fff;
  background-color: #b92f2c;
}

.arrow {
  cursor: pointer;
  color: #000;
  background-color: #f7f7f7;
  border-radius: 8px;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 20px;
  font-weight: bold;
  transition: color .3s;
  display: inline-block;
}

.arrow:hover {
  color: #fff;
  background-color: #b92f2c;
}

.content {
  width: 100%;
  max-width: 1200px;
  background-color: #fff;
  border-radius: 8px;
  flex: 1;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

@media (max-width: 1280px) {
  .content {
    max-width: 90%;
  }
}

@media (max-width: 768px) {
  .content {
    max-width: 100%;
    padding: 15px;
  }

  .page-button {
    margin: 3px;
    padding: 8px 16px;
    font-size: 14px;
  }

  .arrow {
    padding: 8px 16px;
    font-size: 18px;
  }
}

@media (max-width: 320px) {
  .content {
    max-width: 100%;
    padding: 10px;
  }

  .page-button {
    margin: 2px;
    padding: 6px 12px;
    font-size: 12px;
  }

  .arrow {
    padding: 6px 12px;
    font-size: 16px;
  }
}

